.MovieThumbnail a {
    text-decoration: none;
    color: black;
}

.MovieThumbnail {
    font-weight: 400;
}

.MovieThumbnail:hover {
    font-weight: 700;
    box-shadow: 0px 3px 5px grey;
    cursor: pointer;
}

.thumbnailImage {
    transition: all .2s;
}

.MovieThumbnail:hover .thumbnailImage {
    transform: scale(1.2);
}